import React from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { StaticQuery, graphql } from 'gatsby';

import Header from './MainHeader';
import Container from './Container';
import Nav from './Nav';
import Image from './Image';

const HelmetHeader = ({ data }) => (
  <Helmet
    title={data.site.siteMetadata.title}
    meta={[
      { name: 'description', content: data.site.siteMetadata.description },
      { name: 'keywords', content: 'sample, something' },
    ]}
  >
    <html lang="en" />
  </Helmet>
);

const Layout = ({ children, data }) => (
  <StaticQuery
    query={graphql`
    query SiteTitleQuery {
      site{
        siteMetadata{
          logo_url
          description
          title
        }
      }
    }
  `}
    render={data => (
      <React.Fragment>
        <HelmetHeader data={data} />
        <div id="container" className="active">
          <Header>
            <Nav>
              <Container fluid>
                <a className="navbar-brand" href="index.html">
                  <Image src={data.site.siteMetadata.logo_url} roundedCircle />
                  {/* <img src="images/logo.png" alt="" /> */}
                  <h3>{data.site.siteMetadata.title}</h3>
                </a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                  <ul className="navbar-nav ml-auto navigate-sections">
                    <li className="nav-item drop-link">
                      <a className="nav-link active" href="#home-section">Home</a>
                    </li>
                    {/* <li className="nav-item drop-link">
                      <a className="nav-link" href="#about-section">About</a>
                    </li> */}
                    {/* <li className="nav-item drop-link">
                      <a className="nav-link" href="#speakers-section">Speakers</a>
                    </li> */}
                    {/* <li className="nav-item drop-link">
                      <a className="nav-link" href="#blog-section">Blog</a>
                    </li> */}
                  </ul>
                </div>
              </Container>
            </Nav>
          </Header>
          {children}
        </div>
      </React.Fragment>
    )}
  />
);

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout
